import React, { Component } from 'react';
import { Menu, Form, Button } from 'antd';
import { Btn, BtnExport } from 'components/common/button';
import './style.scss';

const ButtonGroup = Button.Group;
@Form.create({ name: 'formMenuTabTop' })
class MenuTabTop extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.menu = (
      <Menu>
        <Menu.Item>
          <a
            target="_parent"
            rel="noopener noreferrer"
            href="#/students/StatisOfRegisNumbers/classHC"
          >
            TK s SV ng k theo lp hnh chnh
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="#/">
            TK s SV ng k theo hc phn
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="#/">
            TK s SV cha ng k
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_parent" rel="noopener noreferrer" href="#/students/StatisOfRegisNumbers">
            TK s SV  ng k
          </a>
        </Menu.Item>
      </Menu>
    );
  }

  handleClick = e => {
    // console.log('click ', e);
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };

  render() {
    const {
      isBtnCancelDisable,
      isBtnRecoveryDisable,
      isBtnTransferDisable,
      isBtnPrintDisable,
      isBtnExportExcelDisable,
      isBtnCancelLoading,
      isBtnRecoveryLoading,
      isBtnTransferLoading,
      isBtnPrintLoading,
      isBtnExportExcelLoading
    } = this.props;
    return (
      <ButtonGroup>
        <Btn
          disabled={isBtnCancelDisable}
          onClick={this.props.handleCancel}
          loading={isBtnCancelLoading}
          icon='close'
        >
          Hy lp
        </Btn>
        <Btn
          disabled={isBtnRecoveryDisable}
          onClick={this.props.handleRecovery}
          loading={isBtnRecoveryLoading}
          icon='rollback'
        >
          Khi phc lp
        </Btn>
        <Btn
          disabled={isBtnTransferDisable}
          onClick={this.props.handleTransfer}
          loading={isBtnTransferLoading}
          icon='swap'
        >
          Chuyn lp
        </Btn>
        <Btn
          disabled={isBtnPrintDisable}
          onClick={this.props.handleExportPdf}
          loading={isBtnPrintLoading}
          icon='printer'
        >
          In bo co
        </Btn>
        <BtnExport
          className="btnExport"
          title="Xut Excel"
          disabled={isBtnExportExcelDisable}
          onClick={this.props.handleExportExcel}
          loading={isBtnExportExcelLoading}
        />
      </ButtonGroup>
    );
  }
}

export default MenuTabTop;



