import React, { Component } from 'react';
import { Row, Col, Form, Checkbox } from 'antd';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { YearSemesterInput, SelectAjax } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';
import { requiredRule } from 'utils/validateRules';
import { getResgisterSessionSelect } from 'services/registerSession';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommon extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };
  
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Nm hc',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Hc k',
          },
        ],
      },
    };
    const trainTypeId = getFieldValue('trainTypeId');
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            form={this.props.form}
            studyField={{ isShow: false }}
            classes={{ isShow: false }}
            optionFieldDecorator={optionFieldDecorator}
          />
          <YearSemesterInput
            form={this.props.form}
            span={5}
            semesterSpan={4}
            optionFieldDecorator={optionFieldDecorator}
          />
          <Col span={5}>
            <Form.Item label="t ng k">
              {getFieldDecorator('registerSessionId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  service={getResgisterSessionSelect}
                  params={{ trainTypeId, yearEduId, semesterId, fullFormat: true }}
                  allowGetData={!!trainTypeId && !!yearEduId && !!semesterId}
                  keyValue="value"
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <Form.Item>
              {getFieldDecorator('isCancelled', {
                valuePropName: 'checked',
                initialValue: false
              })(
                <Checkbox>Lp hy</Checkbox>
              )}
            </Form.Item>
          </Col>
          <Col span={5}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



