import React, { Component } from 'react';
// import { Table } from 'antd';
import { TableData } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
import { getCCSStatistics } from 'services/classCreditSchedule';
// import { setTableStatisCommon } from 'dump/data';

class TableStatisCommon extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchParams: {},
      selectedRowKeys: [],
      tableData: [],
    };

    this.columns = [
      {
        title: 'Lp/Nhm',
        dataIndex: 'syllableCode',
        key: 'syllableCode',
      },
      {
        title: 'Tn hc phn',
        dataIndex: 'subject',
        key: 'subject',
      },
      {
        title: 'S TC',
        dataIndex: 'noOfCredit',
        key: 'noOfCredit',
      },
      {
        title: 'S SV ng k',
        dataIndex: 'noOfEnrolledStudent',
        key: 'noOfEnrolledStudent',
      },
      {
        title: 'S SV ti thiu',
        dataIndex: 'minStudentNo',
        key: 'minStudentNo',
      },
      {
        title: 'S SV ti a',
        dataIndex: 'maxStudentNo',
        key: 'maxStudentNo',
      },
      {
        title: 'Ca hc',
        dataIndex: 'studySession',
        key: 'studySession',
      },
      {
        title: 'Lch hc',
        dataIndex: 'schedule',
        key: 'schedule',
      },
      {
        title: 'Phng hc',
        dataIndex: 'room',
        key: 'room',
      },
      {
        title: 'Gio vin',
        dataIndex: 'teacher',
        key: 'teacher',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = nextProps => {
    const { _time: previousSearchTime } = this.state.searchParams;
    const { _time: currentSearchTime } = getQueryParams();
    if(previousSearchTime !== currentSearchTime) {
      this.handleChangeQueryParams();
    }
    if(this.props.reload !== nextProps.reload) {
      this.setState({ selectedRowKeys: [] })
    }
  };

  onSelectChange = (selectedRowKeys) => {
    const { tableData } = this.state;
    this.setState({ selectedRowKeys });
    const selectedRows = [];
    selectedRowKeys.forEach(index => {
      selectedRows.push(tableData[index]);
    });
    this.props.updateSelectedRows(selectedRows);
  };

  handleChangeQueryParams = () => {
    this.setState({ searchParams: getQueryParams() });
  };

  render() {
    const { searchParams, selectedRowKeys } = this.state;
    const { reload } = this.props;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <TableData
        rowSelection={rowSelection}
        service={async params => {
          const result = await getCCSStatistics(params);
          console.log(result);
          if(result.data) {
            this.setState({ tableData: result.data.items });
          }
          this.props.updateBtnExportDisableState(result.data ? result.data.items : [] );
          return result;
        }}
        paramSearch={searchParams}
        columns={this.columns}
        allowGetData={
          !!searchParams.trainTypeId &&
          !!searchParams.yearEduId &&
          !!searchParams.semesterId &&
          !!searchParams.registerSessionId
        }
        reload={reload}
      />
    );
  }
}

export default TableStatisCommon;



