import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction } from 'components/common';
import {
  cancelActiveClassCredit,
  transferEnrollment,
  exportExcelCCSStatistics,
  exportCCSStatistics,
} from 'services/classCreditSchedule';
import { getQueryParams } from 'utils/urlUtil';
import MenuTabTop from './MenuTabTop';
import FormInfoCommon from './FormInfoCommon';
import TableStatisCommon from './TableStatisCommon';
import './style.scss';

class StatisOfRegisNumbersPage extends Component {
  state = {
    selectedRows: [],
    isBtnCancelDisable: true,
    isBtnRecoveryDisable: true,
    isBtnTransferDisable: true,
    isBtnPrintDisable: true,
    isBtnExportExcelDisable: true,
    isBtnCancelLoading: false,
    isBtnRecoveryLoading: false,
    isBtnTransferLoading: false,
    isBtnPrintLoading: false,
    isBtnExportExcelLoading: false,
    reload: 0,
  };

  updateSelectedRows = selectedRows => {
    console.log(selectedRows);
    const isBtnCancelDisable = selectedRows.filter(row => row.isCancelled === false).length === 0;
    const isBtnRecoveryDisable = selectedRows.filter(row => row.isCancelled === true).length === 0;
    const isBtnTransferDisable = selectedRows.length !== 2;
    console.log(isBtnCancelDisable);
    
    this.setState({
      selectedRows,
      isBtnCancelDisable,
      isBtnRecoveryDisable,
      isBtnTransferDisable,
    });
  }

  handleCancel = async() => {
    try {
      this.setState({ isBtnCancelLoading: true });
      const { selectedRows } = this.state;
      const ccsIds = selectedRows.filter(row => row.isCancelled === false).map(row => row.ccsId);
      await cancelActiveClassCredit({ ccsIds, isCancelled: true });
      this.setState({ reload: this.state.reload + 1 });
      message.success('Hy thnh cng');
      this.setState({ isBtnCancelLoading: false, isBtnCancelDisable: true });
    } catch (err) {
      this.setState({ isBtnCancelLoading: false });
      message.error('C li xy ra');
    }
  }

  handleRecovery = async() => {
    try {
      this.setState({ isBtnRecoveryLoading: true });
      const { selectedRows } = this.state;
      const ccsIds = selectedRows.filter(row => row.isCancelled).map(row => row.ccsId);
      await cancelActiveClassCredit({ ccsIds, isCancelled: false });
      this.setState({ reload: this.state.reload + 1 });
      this.setState({ isBtnRecoveryLoading: false, isBtnRecoveryDisable: true });
      message.success('Khi phc thnh cng');
    } catch (err) {
      this.setState({ isBtnRecoveryLoading: false });
      message.error('C li xy ra');
    }
  }

  handleTransfer = async() => {
    try {
      this.setState({ isBtnTransferLoading: true });
      const { selectedRows } = this.state;
      await transferEnrollment(
        {
          fromCCSId: selectedRows[0].ccsId,
          toCCSId: selectedRows[1].ccsId,
        }
      );
      this.setState({ reload: this.state.reload + 1 });
      this.setState({ isBtnTransferLoading: false, isBtnTransferDisable: true });
      message.success('Chuyn lp thnh cng');
    } catch (err) {
      this.setState({ isBtnTransferLoading: false });
      message.error('C li xy ra');
    }
  }

  handleExportExcel = async() => {
    try {
      this.setState({ isBtnExportExcelLoading: true });
      await exportExcelCCSStatistics(getQueryParams());
      // this.setState({ reload: this.state.reload + 1 });
      this.setState({ isBtnExportExcelLoading: false, isBtnExportExcelDisable: true });
      message.success('Xut bo co thnh cng');
    } catch (err) {
      this.setState({ isBtnExportExcelLoading: false });
      message.error('C li xy ra');
    }
  }

  handleExportPdf = async() => {
    try {
      this.setState({ isBtnPrintLoading: true });
      await exportCCSStatistics(getQueryParams());
      // this.setState({ reload: this.state.reload + 1 });
      this.setState({ isBtnPrintLoading: false, isBtnPrintDisable: true });
      message.success('Xut bo co thnh cng');
    } catch (err) {
      this.setState({ isBtnPrintLoading: false });
      message.error('C li xy ra');
    }
  }

  updateBtnExportDisableState = tableData => {
    this.setState({
      isBtnPrintDisable: tableData.length === 0,
      isBtnExportExcelDisable: tableData.length === 0
    });
  }

  render() {
    const {
      isBtnCancelDisable,
      isBtnRecoveryDisable,
      isBtnTransferDisable,
      isBtnPrintDisable,
      isBtnExportExcelDisable,
      isBtnCancelLoading,
      isBtnRecoveryLoading,
      isBtnTransferLoading,
      isBtnPrintLoading,
      isBtnExportExcelLoading,
      reload
    } = this.state;
    return (
      <Card title="Thng k s lng ng k">
        <CartHeadAction>
          <MenuTabTop
            isBtnCancelDisable={isBtnCancelDisable}
            isBtnRecoveryDisable={isBtnRecoveryDisable}
            isBtnTransferDisable={isBtnTransferDisable}
            isBtnPrintDisable={isBtnPrintDisable}
            isBtnExportExcelDisable={isBtnExportExcelDisable}
            isBtnCancelLoading={isBtnCancelLoading}
            isBtnRecoveryLoading={isBtnRecoveryLoading}
            isBtnTransferLoading={isBtnTransferLoading}
            isBtnPrintLoading={isBtnPrintLoading}
            isBtnExportExcelLoading={isBtnExportExcelLoading}
            handleCancel={this.handleCancel}
            handleRecovery={this.handleRecovery}
            handleTransfer={this.handleTransfer}
            handleExportExcel={this.handleExportExcel}
            handleExportPdf={this.handleExportPdf}
          />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableStatisCommon
              updateSelectedRows={this.updateSelectedRows}
              reload={reload}
              updateBtnExportDisableState={this.updateBtnExportDisableState}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StatisOfRegisNumbersPage />
  </Page>
);



